const ICON_SIZE = 14

const rnrImage1 = require("../../../assets/images/rnr-image-1.png")
const rnrImage2 = require("../../../assets/images/rnr-image-2.png")
const rnrImage3 = require("../../../assets/images/rnr-image-3.png")
const rnrImages = [rnrImage1, rnrImage2, rnrImage3]

export const InvoiceScreen: FC<DemoTabScreenProps<"Deliverynote">> = observer(function InvoiceScreen(_props) {
  
  const { navigation } = _props

  // Pull in one of our MST stores
  const { invoiceStore, authenticationStore: { authToken } } = useStores()
  
  const [refreshing, setRefreshing] = React.useState(false)
  const [isLoading, setIsLoading] = React.useState(false)

  // initially, kick off a background refresh without the refreshing UI
  useEffect(() => {
    ;(async function load() {
      setIsLoading(true)
      invoiceStore.setPageSize(6)
      invoiceStore.setPageCount(0)
      // invoiceStore.setPageIndex(0)
      await invoiceStore.fetchInvoices(decryptAccessToken(authToken), 0)
      setIsLoading(false)
    })()
  }, [invoiceStore])

  // simulate a longer refresh, if the refresh is too fast for UX
  async function manualRefresh() {
    console.log('manualRefresh')
    // setRefreshing(true)
    // deliverynoteStore.setPageIndex(0)
    // await Promise.all([deliverynoteStore.fetchDelnotes(decryptAccessToken(authToken)), delay(750)])
    // setRefreshing(false)

    setRefreshing(true)
    invoiceStore.setInvoices([])
    invoiceStore.setPageSize(6)
    invoiceStore.setPageIndex(0)
    invoiceStore.setPageCount(0)
    await invoiceStore.fetchInvoices(decryptAccessToken(authToken), 0)
    setRefreshing(false)
  }
  async function nextPage() {
    console.log('nextPage')
    setRefreshing(true)
    await Promise.all([invoiceStore.fetchInvoices(decryptAccessToken(authToken), invoiceStore.pageIndex + 1), delay(750)])
    setRefreshing(false)
  }

  // Pull in navigation via hook
  // const navigation = useNavigation()
  return (
    <Screen
      preset="scroll"
      safeAreaEdges={["top"]}
      contentContainerStyle={$screenContentContainer}
    >
      {/* <Text style={$title} preset="heading" tx="deliverynoteScreen.title" /> */}
      <FlatList<Invoice>
        data={invoiceStore.invoicesForList}
        extraData={invoiceStore.invoices.length}
        contentContainerStyle={$flatListContentContainer}
        refreshing={refreshing}
        onRefresh={manualRefresh}
        onEndReachedThreshold={0.2}
        onEndReached={nextPage}
        ListEmptyComponent={
          isLoading ? (<ActivityIndicator />) : (
            <EmptyState
              preset="generic"
              style={$emptyState}
              // headingTx={
              //   episodeStore.favoritesOnly
              //     ? "demoPodcastListScreen.noFavoritesEmptyState.heading"
              //     : undefined
              // }
              headingTx={"demoPodcastListScreen.noFavoritesEmptyState.heading"}
              // contentTx={
              //   episodeStore.favoritesOnly
              //     ? "demoPodcastListScreen.noFavoritesEmptyState.content"
              //     : undefined
              // }
              contentTx={"demoPodcastListScreen.noFavoritesEmptyState.content"}
              // button={episodeStore.favoritesOnly ? null : undefined}
              buttonOnPress={manualRefresh}
              imageStyle={$emptyStateImage}
              ImageProps={{ resizeMode: "contain" }}
            />
          )
        }
        ListHeaderComponent={
          <View style={$heading}>
            <Text preset="heading" tx="invoiceScreen.title" />
            <View style={{
              flex: 1,
              flexDirection: 'row',
              alignContent: 'space-between',
              backgroundColor: colors.palette.neutral300,
              borderRadius: 10,
              marginTop: 10,
            }}>
              <FilteringFormModal locationId="hallooooo" />
              <View style={[styles.centeredView, {marginTop: 0, padding: 5}]}>
                <Pressable
                  style={[styles.button, styles.buttonOpen]}
                  onPress={() => openLinkInBrowser(`${Config.API_URL}udgisi/print-invoice?locationId=${invoiceStore.filterLocationId}&number=${invoiceStore.filterInvoiceNumber}&invoice_year=${invoiceStore.filterYear}&invoice_month=${invoiceStore.filterMonth}&storeId=${invoiceStore.filterStoreId}`)}
                  >
                  <Text style={styles.textStyle}>Print</Text>
                </Pressable>
              </View>
            </View>
            {invoiceStore.isReceiptsNotEmpty && 
                <View style={{
                  flex: 1,
                  flexDirection: 'row',
                  alignContent: 'space-between',
                  backgroundColor: colors.palette.neutral300,
                  borderRadius: 10,
                  marginTop: 10,
                }}>
                  <ReceiptFormModal locationId="hallooooo" />
                  <View style={[styles.centeredView, {marginTop: 0, padding: 5}]}>
                    <Pressable
                      style={[styles.button, styles.buttonOpen]}
                      onPress={async () => {
                        invoiceStore.toggleReceiptReset()
                        await manualRefresh()
                        // deliverynoteStore.setDeliverynotes([])
                        // deliverynoteStore.setPageSize(6)
                        // deliverynoteStore.setPageIndex(0)
                        // deliverynoteStore.setPageCount(0)
                        // await deliverynoteStore.fetchDelnotes(decryptAccessToken(authToken))
                      }}>
                      <Text style={styles.textStyle}>Reset</Text>
                  </Pressable>
                </View>
              </View>
            }
          </View>
        }
        // ListFooterComponent={
        //   <View style={$heading}>
        //     <Text preset="heading" tx="deliverynoteScreen.title" />
        //   </View>
        // }
        renderItem={({ item }) => (
          <ItemCard
            key={item.id}
            invoice={item}
            // isFavorite={episodeStore.hasFavorite(item)}
            isFavorite={false}
            isReceipt={invoiceStore.hasReceipt(item)}
            onPressFavorite={() => {
              
            }}
            onPressAddToReceipt={() => {
              invoiceStore.toggleReceipt(item)
            }}
            // onPressFavorite={() => episodeStore.toggleFavorite(item)}
            onPressCard={async () => {
              // deliverynoteStore.setCreateFormSubmitInprogress(false)
              // deliverynoteStore.setCreateFormSubmitMessage("")
              // deliverynoteStore.setCreateFormSubmitErrorMessage("")
              // deliverynoteStore.setCreateFormSubmitSuccessMessage("")
              // deliverynoteStore.setCreateFormId(item.id)
              // deliverynoteStore.setcreateFormDeliveryNoteNumber(item.nomor_surat)
              // deliverynoteStore.setCreateFormTotalCase(item.total_case)
              // deliverynoteStore.setCreateFormLocationId(item.store.location.id)
              // deliverynoteStore.setCreateFormStoreId(item.store.id)
              // deliverynoteStore.setCreateFormDeliveryYear(''+item.delivery_year)
              // deliverynoteStore.setCreateFormDeliveryMonth(''+item.delivery_month)
              // deliverynoteStore.setCreateFormDeliveryDate(''+item.delivery_date)
              // await deliverynoteStore.fetchCreateFormStores(decryptAccessToken(authToken))
              // deliverynoteStore.setCreateFormModalVisible(true)
              // navigation.navigate("InvoiceDetail")
            }}
            // onPressCard={() => navigation.navigate("deliverynoteDetail", { episode: item })}
          />
        )}
      />
    </Screen>
  )
})

const ItemCard = observer(function ItemCard({
  invoice,
  isFavorite,
  onPressFavorite,
  onPressAddToReceipt,
  onPressCard,
  isReceipt
}: {
  invoice: Invoice
  onPressFavorite: () => void
  onPressAddToReceipt: () => void
  onPressCard: () => void
  isFavorite: boolean
  isReceipt: boolean
}) {
  const liked = useSharedValue(isFavorite ? 1 : 0)
  const invoiceAdded = useSharedValue(0)
  const receiptAdded = useSharedValue(isReceipt ? 1 : 0)
  const imageUri = useMemo(() => {
    return rnrImages[Math.floor(Math.random() * rnrImages.length)]
  }, [])

  // Grey heart
  const animatedLikeButtonStyles = useAnimatedStyle(() => {
    return {
      transform: [
        {
          scale: interpolate(liked.value, [0, 1], [1, 0], Extrapolate.EXTEND),
        },
      ],
      opacity: interpolate(liked.value, [0, 1], [1, 0], Extrapolate.CLAMP),
    }
  })
  const animatedUnlikeButtonStyles = useAnimatedStyle(() => {
    return {
      transform: [
        {
          scale: liked.value,
        },
      ],
      opacity: liked.value,
    }
  })
  
  const animatedReceiptInButtonStyles = useAnimatedStyle(() => {
    return {
      transform: [
        {
          scale: interpolate(receiptAdded.value, [0, 1], [1, 0], Extrapolate.EXTEND),
        },
      ],
      opacity: interpolate(receiptAdded.value, [0, 1], [1, 0], Extrapolate.CLAMP),
    }
  })

  // Pink heart
  const animatedReceiptOutButtonStyles = useAnimatedStyle(() => {
    return {
      transform: [
        {
          scale: receiptAdded.value,
        },
      ],
      opacity: receiptAdded.value,
    }
  })

  const animatedInvoiceInButtonStyles = useAnimatedStyle(() => {
    return {
      transform: [
        {
          scale: interpolate(invoiceAdded.value, [0, 1], [1, 0], Extrapolate.EXTEND),
        },
      ],
      opacity: interpolate(invoiceAdded.value, [0, 1], [1, 0], Extrapolate.CLAMP),
    }
  })

  // Pink heart
  const animatedInvoiceOutButtonStyles = useAnimatedStyle(() => {
    return {
      transform: [
        {
          scale: invoiceAdded.value,
        },
      ],
      opacity: invoiceAdded.value,
    }
  })

  

  /**
   * Android has a "longpress" accessibility action. iOS does not, so we just have to use a hint.
   * @see https://reactnative.dev/docs/accessibility#accessibilityactions
   */
  const accessibilityHintProps = useMemo(
    () =>
      Platform.select<AccessibilityProps>({
        ios: {
          accessibilityHint: translate("demoPodcastListScreen.accessibility.cardHint", {
            action: isFavorite ? "unfavorite" : "favorite",
          }),
        },
        android: {
          accessibilityLabel: invoice.nomor_invoice,
          accessibilityActions: [
            {
              name: "longpress",
              label: translate("demoPodcastListScreen.accessibility.favoriteAction"),
            },
          ],
          onAccessibilityAction: ({ nativeEvent }) => {
            if (nativeEvent.actionName === "longpress") {
              handlePressFavorite()
            }
          },
        },
      }),
    [invoice, isFavorite],
  )

  const handlePressFavorite = () => {
    onPressFavorite()
    liked.value = withSpring(liked.value ? 0 : 1)
  }
  const handlePressInvoice = () => {
    // onPressAddToInvoie()
    // invoiceAdded.value = withSpring(invoiceAdded.value ? 0 : 1)
  }
  const handlePressReceipt = () => {
    onPressAddToReceipt()
    receiptAdded.value = withSpring(receiptAdded.value ? 0 : 1)
  }

  const handlePressCard = () => {
    // openLinkInBrowser(episode.enclosure.link)
    onPressCard()
  }

  const ButtonLeftAccessory = useMemo(
    () =>
      function ButtonLeftAccessory() {
        return (
          <View>
            <Animated.View
              style={[$iconContainer, StyleSheet.absoluteFill, animatedLikeButtonStyles]}
            >
              <Icon
                icon="heart"
                size={ICON_SIZE}
                color={colors.palette.neutral800} // dark grey
              />
            </Animated.View>
            <Animated.View style={[$iconContainer, animatedUnlikeButtonStyles]}>
              <Icon
                icon="heart"
                size={ICON_SIZE}
                color={colors.palette.primary400} // pink
              />
            </Animated.View>
          </View>
        )
      },
    [],
  )
  const ButtonLeftInvoiceAccessory = useMemo(
    () =>
      function ButtonLeftInvoiceAccessory() {
        return (
          <View>
            <Animated.View
              style={[$iconContainer, StyleSheet.absoluteFill, animatedInvoiceInButtonStyles]}
            >
              <Icon
                icon="podcast"
                size={ICON_SIZE}
                color={colors.palette.neutral800} // dark grey
              />
            </Animated.View>
            <Animated.View style={[$iconContainer, animatedInvoiceOutButtonStyles]}>
              <Icon
                icon="podcast"
                size={ICON_SIZE}
                color={colors.palette.primary400} // pink
              />
            </Animated.View>
          </View>
        )
      },
    [],
  )
  const ButtonLeftReceiptAccessory = useMemo(
    () =>
      function ButtonLeftReceiptAccessory() {
        return (
          <View>
            <Animated.View
              style={[$iconContainer, StyleSheet.absoluteFill, animatedReceiptInButtonStyles]}
            >
              <Icon
                icon="clap"
                size={ICON_SIZE}
                color={colors.palette.neutral800} // dark grey
              />
            </Animated.View>
            <Animated.View style={[$iconContainer, animatedReceiptOutButtonStyles]}>
              <Icon
                icon="clap"
                size={ICON_SIZE}
                color={colors.palette.primary400} // pink
              />
            </Animated.View>
          </View>
        )
      },
    [],
  )
  return (
    <Card
      style={$item}
      verticalAlignment="force-footer-bottom"
      onPress={handlePressCard}
      onLongPress={handlePressFavorite}
      HeadingComponent={
        <View style={$metadata}>
          <Text
            style={$metadataText}
            size="xxs"
          >
            {/* {`${delnote.storeLocationName} / ${invoice.deliveryYearMonth} / ${invoice.store.store_name}`} */}
            {/* {`${invoice.storeLocationName} / ${invoice.deliveryYearMonth} / ${invoice.store.store_name} / total case ${delnote.total_case}`} */}
          </Text>
          {/* <Text
            style={$metadataText}
            size="xxs"
            accessibilityLabel={'tesssting'}
            // accessibilityLabel={episode.duration.accessibilityLabel}
          >
            {episode.duration.textLabel}
          </Text> */}
        </View>
      }
      content={`${invoice.nomor_invoice}`}
      {...accessibilityHintProps}
      // RightComponent={<Image source={imageUri} style={$itemThumbnail} />}
      FooterComponent={
        <View style={{ flexDirection: "row" }}>
          <Button
            onPress={handlePressReceipt}
            onLongPress={handlePressReceipt}
            style={[$favoriteButton, isFavorite && $unFavoriteButton]}
            accessibilityLabel={
              isFavorite
                ? translate("demoPodcastListScreen.accessibility.unfavoriteIcon")
                : translate("demoPodcastListScreen.accessibility.favoriteIcon")
            }
            LeftAccessory={ButtonLeftReceiptAccessory}
          >
            <Text
              size="xxs"
              weight="medium"
              text={!invoice.isReceiptCreated ? 'Add to Receipt' : invoice.receipt?.nomor_kwitansi}
            />
          </Button>
        </View>
      }
    />
  )
})
